import Vue from 'vue'
import VueRouter from 'vue-router'
import User from "@/pages/User";
import Home from "@/pages/Home";
import ShopCart from "@/pages/ShopCart";
import CateGory from "@/pages/CateGory";

Vue.use(VueRouter)

const routes = [
    {path: "/", redirect: "/home", meta: {title: "首页"}},
    {path: "/home", name: "Home", component: Home, meta: {title: "首页"}},
    {path: "/cateGory", name: "CateGory", component: CateGory, meta: {title: "分类"}},
    {path: "/shopCart", name: "ShopCart", component: ShopCart, meta: {title: "购物车"}},
    {path: "/user", name: "User", component: User, meta: {title: "我的"}}
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
